


















import Vue from 'vue'
import Expenses from './Expenses.vue'
import AddExpenseDialog from './AddExpenseDialog.vue'

export default Vue.extend({
  name: 'HelloWorld',
  props: {
    category: Object
  },
  components: {
    Expenses,
    AddExpenseDialog
  },
  data () {
    return {
      newExpenseName: '',
      newExpenseAmount: null
    }
  },
  methods: {
    fetchCategories () {
      this.$emit('newCategoryAdded')
    },
    clearInputs () {
      this.newExpenseName = ''
      this.newExpenseAmount = null
    }
  }
})
